
import Msg from 'vux/src/components/msg/index.vue'

export default {
name:'logout',
data(){
return{
isShowMsg: false,
buttons:[
{
type: 'primary',
text: '跳转到登录页面',
link: '/login'
}
]
}
},
components:{
Msg
},

created:function(){

if( false == this.$store.state.isLogined){
this.isShowMsg = true;
return;
}


this.$store.commit('mutationLogout');

window.localStorage.clear();

this.$router.push('/topiclist/all');
}
}



